<template>
  <div :class="['nav-bar', {'nav-show': navShow}]">
    <div class="bin bb">
      <div class="left">
        <p>米斯特王</p>
      </div>
      <div class="right"
           @click="clickNavShow">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    <nav>
      <div class="item"
           v-for="item in navList"
           :key="item.id">
        <p @click="routeLink(item.link)">{{item.name}}</p>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'nav-bar',
  data() {
    return {
      navShow: false,
      navList: [
        {
          id: 1,
          name: '首页',
          icon: '',
          link: 'blogs'
        },
        {
          id: 2,
          name: '归档',
          icon: '',
          link: 'archives'
        },
        {
          id: 3,
          name: '分类',
          icon: '',
          link: 'categories'
        },
        {
          id: 4,
          name: '标签',
          icon: '',
          link: 'tags'
        },
        {
          id: 5,
          name: '关于',
          icon: '',
          link: 'about'
        },
        {
          id: 6,
          name: '搜索',
          icon: '',
          link: ''
        }
      ]
    };
  },
  components: {},
  computed: {},
  watch: {},
  methods: {
    clickNavShow() {
      this.navShow = !this.navShow;
    },
    routeLink(link) {
      this.navShow = false;
      let path = this.$route.path;
      if (`/${link}` == path) return;
      this.$router.push({path: link});
    }
  },
  created() {},
  mounted() {},
  updated() {},
  destroyed() {}
};
</script>
